html, body, #root {
  width: 100vw;
  height: 100vh;
  background-image: url('./images/wood-bg.jpg');
  background-position: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /*background-color: #eee;*/
}

.survey {
  position: relative;
  height: 100vh;
  /*background-color: #eee;*/
}

.tgj {
  text-align: left;
  margin-left: 30px;
}

.tgj-logo {
  width: 40px;
}

.tgj-name {
  display: inline-block;
  vertical-align: top;
  color: #58595b;
  padding: 0 0 0 5px;
}

.tgj-name-graphic {
  color: #69e2d3;
}

.question-sec {
  width: 74.5%;
  display: inline-block;
  text-align: center;
  padding: 20px 0 60px;
  background-color: #fff;
  margin: 0 2.5% 30px;
  border-radius: 5px;
  position: absolute;
  top: 30px;
  /*transform: translateY(-50%);*/
  box-shadow: 0 0 10px .3px rgba(0, 0, 0, 0.4);
  border: 2px solid #72cbcd;
}

.step-7 {
  width: 95%;
}

.questions-header {
  font-size: 40px;
  font-weight: bold;
  margin: 0;
}

.questions-minor {
  font-size: 24px;
}

.input-column {
  display: inline-block;
}

.input-column-right {
  margin-right: 50px;
}

.inputs, .other-input {
  border: none;
  border-bottom: 1px solid #000;
  font-size: 18px;
  /*border-radius: 5px;*/
}

.inputs {
  width: 300px;
}

.inputs:focus {
  outline-width: 0;
}

.textarea {
  border: 1px solid #000;
  border-radius: 5px;
  font-size: 18px;
  width: 60%;
}

.textarea:focus {
  outline-width: 0;
}

.primary-color, .secondary-color {
  display: inline-block;
}

.primary-color {
  vertical-align: top;
}

.secondary-color {
  margin-left: 40px;
}

.buttons {
  display: inline-block;
  vertical-align: top;
  width: 350px;
  height: auto;
  border: 5px solid #5dafaf;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px;
  text-align: center;
  margin: 60px 10px 0;
  /*background-image: linear-gradient(#a3e2e2, #a3e2e2 40%, #6bc6c6 80%);*/
  background-image: linear-gradient(to bottom right,#6bc6c6, #b9eae9);
}

.buttons-proj {
  height: 250px;
  width: 250px;
}

.buttons-proj:nth-child(3) {
  margin: 60px 5px 0 0;
}

.buttons-proj:nth-child(4) {
  margin: 60px 0 0 5px;
}

.buttons:hover {
  transform: translateY(-2%);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.8);
}

.buttons-title {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 0;
}

.buttons-price {
  font-size: 26px;
  color: green;
  font-weight: bold;
  margin-top: 0;
}

.buttons-desc {
  font-size: 20px;
}

.logo-simple {
  width: 300px;
}

.logo-intricate {
  width: 325px;
}

.small-button {
  border: 2px solid #eeca68;
  padding: 5px 15px;
  cursor: pointer;
  font-size: 22px;
  border-radius: 5px;
  display: inline-block;
  color: #eeca68;
}

.next-button {
  margin-left: 20px;
}

.back-button {
  font-size: 22px;
  border: 2px solid #eeca68;
  display: inline-block;
  padding: 5px 15px;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 5px;
  color: #eeca68;
}

.back-button:hover, .next-button:hover, .small-button:hover {
  background-color: #eeca68;
  color: #fff;
}

.fa-bars {
  font-size: 30px;
  color: #72cbcd;
  cursor: pointer;
  text-align: right;
}

.summary-column { 
  width: 20%;
  height: 80%;
  display: inline-block;
  vertical-align: top;
  background-color: #fff;
  position: fixed;
  right: 0;
  top: 30px;
  margin: 0 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px .3px rgba(0, 0, 0, 0.4);
  border: 2px solid #72cbcd;
}

.summary-title {
  margin: 20px auto;
  padding-bottom: 10px;
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  border-bottom: 1px solid #000;
  width: 80%;
}

.category-title {
  font-size: 22px;
  padding-left: 15px;
  font-weight: bold;
  color: #72cbcd;
  cursor: pointer;
  margin-bottom: 0;
  text-decoration: underline;
}

.category-desc {
  font-size: 18px;
  padding-left: 40px;
  margin: 0;
}

.est-price {
  position: fixed;
  bottom: 30px;
  right: 20px;
  width: 20%;
  background-color: green;
  color: #fff;
  font-size: 24px;
  font-weight: bold;
  padding: 10px 0;
  text-align: center;
  border-radius: 5px;
  box-shadow: 0 0 10px .3px rgba(0, 0, 0, 0.4);
}

.end-summary-sec {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  text-align: left;
}

.end-summary-sec:nth-child(3),
.end-summary-sec:nth-child(4) {
  margin-right: 100px;
}

.end-summary-title {
  font-weight: bold;
}

@media screen and (max-width: 1080px) {
  .question-sec {
    width: 94vw;
  }

  .input-column-right {
    margin: 0;
  }

  .questions-header {
    width: 90%;
    margin: 20px auto 0;
  }

  .questions-minor {
    font-size: 20px;
    width: 80%;
    margin: 20px auto;
  }

  .summary-column {
    margin-top: 10px;
    width: auto;
    height: auto;
    padding: 8px;
  }

  .category-title {
    padding-left: 5px;
  }

  .category-desc {
    padding-left: 25px;
  }

  .est-price {
    width: 100%;
    position: static;
    font-size: 18px;
    margin-top: 30px;
    box-shadow: none;
  }

  .end-summary-sec {
    margin: auto!important;
    width: 60%;
    display: block;
  }
}

@media screen and (max-width: 500px) {
  .tgj {
    text-align: center;
  }

  .questions-header {
    width: 100%;
    font-size: 30px;
  }

  .inputs {
    width: 230px;
  }

  .textarea {
    width: 300px;
  }

  .secondary-color {
    margin-left: 0;
  }

  .buttons-proj {
    width: 200px!important;
    height: 200px;
    margin: 30px 0 0!important;
  }

  .buttons {
    width: 250px;
  }

  .buttons-title {
    font-size: 26px;
  }

  .buttons-price {
    font-size: 24px;
  }

  .logo-simple {
    width: 200px;
  }

  .logo-intricate {
    width: 250px;
  }

  .end-summary-sec {
    width: 80%;
  }
}